// lifecycle
import { useState, useEffect, useRef } from 'react';

// popup
import BookingPopup from './booking_popup/BookingPopup';

// components
import Navbar from "./navbar/Navbar";
import Homepage from "../screens/Homepage";
import Footer from "./footer/Footer";
import Credits from "./credits/Credits";

// functions
import CircleMouseShape from '../function/CircleMouseShape';

// style
import './HomeLayout.scss';

function HomeLayout(){

    // popup
    const [showPopup, setShowPopup] = useState(false);
    // function to show popup
    function showPopupFunction(booleanValue){
        setShowPopup(booleanValue);
    }
    // function to hide popup
    function hidePopupFunction(){
        setShowPopup(false);
    }

    // circle cursor options
    const [showCircleCursor, setShowCircleCursor] = useState(false);
    const [circleCursorContect, setCircleCursorContent] = useState('View');
    // function to show circle cursor
    function showCircleCursorFunction(boolValue, contentText){
        setShowCircleCursor(boolValue);
        setCircleCursorContent(contentText);
        // console.log(boolValue)
    }

    // set footer height and animation
    const [footerHeight, setFooterHeight] = useState(0);
    const footerComponent = useRef(null);
    const [footerFromTop, setFooterFromTop] = useState(0);
    const [footerTransition, setFooterTransition] = useState('-100%');
    // 
    useEffect(() => {
        setFooterHeight(footerComponent.current.clientHeight);

        window.addEventListener('resize', () => {
            setFooterHeight(footerComponent.current.clientHeight); 
        })

    }, [footerHeight])

    useEffect(() => {
        setFooterFromTop(parseInt(footerComponent.current.getBoundingClientRect().top));

        window.addEventListener('resize', () => {
            setFooterFromTop(footerComponent.current.getBoundingClientRect().top);
        })

        window.addEventListener('scroll', () => {
            // console.log(footerDistance);
            // const footerFromTop = footerComponent.current.getBoundingClientRect().top;
            setFooterFromTop(parseInt(footerComponent.current.getBoundingClientRect().top));
            if(footerFromTop <= footerHeight){
                // console.log(footerHeight + ' >= ' + footerFromTop);
                setFooterTransition(((footerHeight - footerFromTop)/footerHeight)*100 - 100)
                // console.log(footerTransition);
            }
        })
    }, [footerFromTop, footerHeight])
    

    return( 
        <div>

            {/* circle cursor */}
            <CircleMouseShape showCircleCursor={showCircleCursor} circleCursorContect={circleCursorContect}/>

            <BookingPopup shown={showPopup} hidePopupFunction={hidePopupFunction}/>
 
            <Navbar showPopupFunction={showPopupFunction}/>
            <Homepage showCircleCursorFunction={showCircleCursorFunction}/>
            <div className='footerContainer' style={{height: footerHeight}}>
                <div className='footerAnimation' ref={footerComponent}>
                    <div className='animatedFooter' style={{transform: `translateY(${footerTransition < -75 ? (footerTransition + '%') : '0%'})`}}>
                        <Footer />
                        <Credits />
                    </div>
                </div>
            </div>

        </div>
    )
}

export default HomeLayout;