// style
import './Navbar.scss';

// logo
import Logo from '../../assets/Logo';

// lifecycle
import { useState, useEffect } from 'react';

// translation
import { useTranslation } from 'react-i18next';
import MobileNavbar from '../../components/MobileNavbar';

function Navbar(props) {

  // get scroll positioin and direction
  const [scrollY, setScrollY] = useState(0);
  const [scrollUp, setScrollup] = useState(true);

  // translation
  const { t, i18n } = useTranslation();

  
  // function to change language by clicking button
  function changeLang(){
    if (i18n.language === 'ar') {
      // i18n.changeLanguage('en');
      // setCurrentLanguage('en');
      window.location.href = './en';
      // console.log('1')
    }else{
      // i18n.changeLanguage('ar');
      // setCurrentLanguage('ar');
      window.location.href = './ar';
      // console.log('2')
    }
    // window.scrollTo(0,0);
    // window.location.reload();
  }

  useEffect(() => {
    window.addEventListener('scroll', (event) => {
      let newScrollY = document.documentElement.scrollTop;
      if (newScrollY > scrollY){
        setScrollup(false)
      }else{
        setScrollup(true)
      }
      if (newScrollY > scrollY){
        setScrollup(false)
      }
      setScrollY(newScrollY);
    })
  })

  return (

    <div className={`Navbar ${scrollY >= 10 ? 'notTop' : ''} ${scrollUp ? '' : 'hide'}`}>
    {/* <div className="Navbar"> */}

      <Logo />

      <ul className='navbarLinks'>
        <li>
            <a href='#homepageTop'>{t('navbar.home')} </a>
        </li>
        <li>
            <a href='#homepageAboutUS'>{t('navbar.about_us')} </a>
        </li>
        <li>
            <a href='#homepageOurServices'>{t('navbar.services')} </a>
        </li>
        <li>
            <a href='#homepageOurProjects'>{t('navbar.our_work')} </a>
        </li>
        {/* noteeeee: the following links are being hidden, don't delete them plsss */}
        {/* 
        li> <a href='#'>{t('navbar.our_products')} </a>
        </li>
        <li>
        <a href='#'>{t('navbar.careers')} </a>
        </li>
        <li>
        <a href='#'>{t('navbar.insights')} </li</a>
        > */}
        <li>
            <a href='#homepageContactUs'>{t('navbar.contact_us')} </a>
        </li>
      </ul>

      <div className='navbarOptions'>
        <div className={`navbarLanguage ${i18n.language}`} onClick={changeLang}> {i18n.language} </div>
        <button className='bookAMeeting' onClick={() => props.showPopupFunction(true)}>{t('navbar.book_a_meeting')}</button>
        <MobileNavbar scrollUp={scrollUp}/>
      </div>


    </div>
  );
}

export default Navbar;
