// style
import './MobileNavbar.scss';

// lifecycle
import { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';

// translation


function MobileNavbar(props){

    // translation
    const { t } = useTranslation();

    // variable to toggle navbar
    const [showNavbar, setShowNavbar] = useState(false);

    function toggleMobileNavbar(){
        // alert('toggle mobile navbar');
        if(showNavbar){
            setShowNavbar(false);
        }else{
            setShowNavbar(true);
        }
    }

    function closeMobileNavbar(){
        setShowNavbar(false);
    }

    useEffect(() => {
        setShowNavbar(false);
    }, [props.scrollUp])

    return(
        <div className='mobileNavContainer'>
            <div className='toggleMobileNav' onClick={() => toggleMobileNavbar()}>
            <div className='toggleLine' style={{transform: showNavbar ? 'rotateZ(45deg) translateX(6px) translateY(1px)' :''}}></div>
            <div className='toggleLine' style={{transform: showNavbar ? 'rotateZ(-45deg) translateX(6px) translateY(-1px)' :''}}></div>

            </div>
            <div className='mobileNavbarBody' style={{opacity: showNavbar ? '1' : '0', pointerEvents: showNavbar ? 'all' : 'none'}}>
                <ul className='mobileNavbarLinks'>
                    <li>
                        <a onClick={() => closeMobileNavbar()} href='#homepageTop'>{t('navbar.home')} </a>
                    </li>
                    <li>
                        <a onClick={() => closeMobileNavbar()} href='#homepageAboutUS'>{t('navbar.about_us')} </a>
                    </li>
                    <li>
                        <a onClick={() => closeMobileNavbar()} href='#homepageOurServices'>{t('navbar.services')} </a>
                    </li>
                    <li>
                        <a onClick={() => closeMobileNavbar()} href='#homepageOurProjects'>{t('navbar.our_work')} </a>
                    </li>
                    {/* noteeeee: the following links are being hidden, don't delete them plsss */}
                    {/* 
                    li> <a onClick={() => closeMobileNavbar()} href='#'>{t('navbar.our_products')} </a>
                    </li>
                    <li>
                    <a onClick={() => closeMobileNavbar()} href='#'>{t('navbar.careers')} </a>
                    </li>
                    <li>
                    <a onClick={() => closeMobileNavbar()} href='#'>{t('navbar.insights')} </li</a>
                    > */}
                    <li>
                        <a onClick={() => closeMobileNavbar()} href='#homepageContactUs'>{t('navbar.contact_us')} </a>
                    </li>
                </ul>
            </div>
        </div>
    )
}

export default MobileNavbar