// style
import './ContactUs.scss'

// lifecycle
import { useEffect, useState } from 'react';

// translation
import { useTranslation } from 'react-i18next';

// country codes
import countryCodes from '../../locales/json_files/countrycodes.json';
// react select
import Select from 'react-select';


// images
import ImageItemNoAnimation from '../../assets/ImageItemNoAnimation';
// img
import { EnvelopeIcon, PhoneIcon, MapPinIcon } from '@heroicons/react/24/solid';

function ContactUs(){

    // translation
    const { t } = useTranslation();
    // set static text
    const [contactUs, setContactUs] = useState('');
    const [sloganContactUs, setSloganContactUs] = useState('');
    //
    const [fullNameLabel, setFullNameLabel] = useState('');
    const [emailLabel, setEmailLabel] = useState('');
    const [companyLabel, setcompanyLabel] = useState('');
    const [phoneLabel, setPhoneLabel] = useState('');
    const [yourMessage, setyourMessage] = useState('');
    const [submitButton, setSubmitButton] = useState('');

    useEffect(() => {
        setContactUs(t("navbar.contact_us"));
        setSloganContactUs(t("home.contact_us.team_help_services"));setFullNameLabel(t("popup.personal_info_form.full_name"));
        setEmailLabel(t("popup.personal_info_form.email"));
        setcompanyLabel(t("popup.personal_info_form.company"));
        setPhoneLabel(t("popup.personal_info_form.phone"));
        setyourMessage(t("popup.personal_info_form.your_message"));
        setSubmitButton(t("popup.personal_info_form.submit_info"));
    }, [t])

    return(
        <div>
            <div className='homepageContactUs' id='homepageContactUs'>
                <div className='contactUsTop'>
                    <div className='sectionTitle'>{contactUs}</div>
                    <div className='sectionSlogan wow fadeInUp'>{sloganContactUs}</div>
                </div>
                <div className='contactDetails'>
                    <div className='contactItem'>
                        <div className='contactIcon wow fadeInRight'>
                            <EnvelopeIcon />
                        </div>
                        <div className='contactText wow fadeInUp'>info@BigMedia.com</div>
                    </div>
                    <div className='contactItem'>
                        <div className='contactIcon wow fadeInRight'>
                            <PhoneIcon />
                        </div>
                        <div className='contactText wow fadeInUp'>+971 55 387 9113</div>
                    </div>
                    <div className='contactItem'>
                        <div className='contactIcon wow fadeInRight'>
                            <MapPinIcon />
                        </div>
                        <div className='contactText wow fadeInUp'>Al Barsha, Mall Of The Emirates</div>
                    </div>
                </div>
                <form className='contactForm'>
                        <div className='inputContainer'>
                            <input required placeholder=" " name="name" type='text' pattern='[A-Za-z0-9 ]{6,25}$'/>
                            <label>{fullNameLabel}</label>
                        </div>
                        <div className='inputContainer'>
                            <input required placeholder=" " name="company" type='text' pattern='[A-Za-z0-9 ]{6,25}$'/>
                            <label>{companyLabel}</label>
                        </div>
                        <div className='inputContainer'>
                            <input required placeholder=" " name="email" type='email' pattern='[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$'/>
                            <label>{emailLabel}</label>
                        </div>
                        {/* <div className='inputContainer phone'>
                            <input required placeholder=" " name="phone" type='tel' pattern="[0-9+]{9,25}$"/>
                            <label>{phoneLabel}</label>
                        </div> */}

                        <div className='inputContainer'>
                            <div className='phoneInput'>
                                <Select
                                    name="countrycode"
                                    required
                                    options={countryCodes.map((i, index) => ({
                                        label:
                                            <div className='countryCodeSelectOption' key={index}>
                                                <ImageItemNoAnimation imageLink={'country_flags/'+(i.code).toLowerCase()+'.svg'}/>
                                                <div>{i.dial_code}</div>
                                            </div>,
                                        value: i.dial_code
                                    }))}
                                    defaultValue={{
                                        label:
                                            <div className='countryCodeSelectOption'>
                                                <ImageItemNoAnimation imageLink={'country_flags/ae.svg'}/>
                                                <div>+971</div>
                                            </div>,
                                        value: +971
                                    }}
                                />
                                <input name="phone" required placeholder=" " type="tel" pattern="[0-9]{9,25}$"/>
                            </div>
                            <label>{phoneLabel}</label>
                        </div>
                        <div className='inputContainer textareaContainer'>
                            <textarea rows="3" placeholder=" "></textarea>
                            <label>{yourMessage}</label>
                        </div>
                    <div className='submitContactForm'>
                        <button className='wow fadeInUp'>{submitButton}</button>
                    </div>
                </form>
            </div>
        </div>
    )
}

export default ContactUs;
