// style
import './MeetingPersonalForm.scss';

// lifecycle
import { useState, useEffect } from 'react';

// country codes
import countryCodes from '../../locales/json_files/countrycodes.json';
// react select
import Select from 'react-select';

// svg
import ArrowBack from '../../assets/ArrowBack';

// images
import ImageItemNoAnimation from '../../assets/ImageItemNoAnimation';

// translation
import { useTranslation } from 'react-i18next';

// components
import InputContainer from '../../components/InputContainer';

function MeetingPersonalForm(props){

    // translation
    const { t } = useTranslation();
    const [personalInfoTitle, setPersonalnfoTitle] = useState('');
    const [fullNameLabel, setFullNameLabel] = useState('');
    const [emailLabel, setEmailLabel] = useState('');
    const [companyLabel, setcompanyLabel] = useState('');
    const [phoneLabel, setPhoneLabel] = useState('');
    const [interestAreaLabel, setInterestAreaLabel] = useState('');
    const [projectBriefLabel, setProjectBriefLabel] = useState('');
    const [eidtButton, setEditButton] = useState('');
    const [backButton, setBackButton] = useState('');
    const [submitButton, setSubmitButton] = useState('');

    useEffect(() => {
        setPersonalnfoTitle(t("popup.personal_info_form.personal_info_title"));
        setFullNameLabel(t("popup.personal_info_form.full_name"));
        setEmailLabel(t("popup.personal_info_form.email"));
        setcompanyLabel(t("popup.personal_info_form.company"));
        setPhoneLabel(t("popup.personal_info_form.phone"));
        setInterestAreaLabel(t("popup.personal_info_form.interest_area"));
        setProjectBriefLabel(t("popup.personal_info_form.project_brief"));
        setEditButton(t("popup.personal_info_form.edit"));
        setBackButton(t("popup.personal_info_form.back"));
        setSubmitButton(t("popup.personal_info_form.submit_info"));
    }, [t])

    // interest select
    const [interestSelectOptions, setinterestSelectOptions] = useState([
        {label: "Branding and Content Creation", value: "Branding and Content Creation"},
        {label: "Digital Marketing Services", value: "Digital Marketing Services"},
        {label: "Web Development", value: "Web Development"},
        {label: "Social Media Strategy", value: "Social Media Strategy"},
        {label: "Video Creation", value: "Video Creation"},
        {label: "Photography", value: "Photography"}
    ]);

    return(
        <form className={`meetingForm ${props.showPersonel? '': 'hide'}`}>
            <div className={'meetingPersonalInfo'}>
                <div className='meetingPersonalTitle'>{personalInfoTitle}</div>
                <div className='meetingChosenDate'>
                    <div>{`${props.dateValue}, ${props.meetingHour}`}</div>
                    {/* <div>{`${props.formattedDateValue}`}</div> */}
                    <div className='meetingEditButton' onClick={() => props.showPersonelFunction()}>{eidtButton}</div>
                </div>
                <div className='hiddenInputs'>
                    <input name="date" type="date" readOnly value={`${props.dateInput}`}/>
                    <input name="duration" type="text" readOnly value={props.meetingDuration}/>
                    <input name="hour" type="text" readOnly value={props.meetingHour}/>
                </div>
                <div className='meetingPersonalForm'>
                    {/* <div className='inputContainer'>
                        <input name="name" required placeholder=" " type="text" pattern="[A-Za-z0-9 ]{6,25}$"/>
                        <label>{fullNameLabel}</label>
                    </div> */}
                    <InputContainer type={'text'} name={'name'} pattern={'[A-Za-z0-9 ]{6,25}$'} labelName={fullNameLabel}/>
                    <InputContainer type={'email'} name={'email'} pattern={'[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$'} labelName={emailLabel}/>
                    <InputContainer type={'text'} name={'companyname'} pattern={'[A-Za-z0-9_- ]{6,25}$'} labelName={companyLabel}/>
                    {/* <div name="email" className='inputContainer'>
                        <input required placeholder=" " type="email" pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$"/>
                        <label>{emailLabel}</label>
                    </div> */}
                    {/* <div className='inputContainer'>
                        <input name="companyname" required placeholder=" " type="text"/>
                        <label>{companyLabel}</label>
                    </div> */}
                    <div className='inputContainer'>
                        <div className='phoneInput'>
                            <Select
                                name="countrycode"
                                required
                                options={countryCodes.map((i, index) => ({
                                    label:
                                        <div className='countryCodeSelectOption' key={index}>
                                            <ImageItemNoAnimation imageLink={'country_flags/'+(i.code).toLowerCase()+'.svg'}/>
                                            <div>{i.dial_code}</div>
                                        </div>,
                                    value: i.dial_code
                                }))}
                                defaultValue={{
                                    label:
                                        <div className='countryCodeSelectOption'>
                                            <ImageItemNoAnimation imageLink={'country_flags/ae.svg'}/>
                                            <div>+971</div>
                                        </div>,
                                    value: +971
                                }}
                            />
                            <input name="phone" required placeholder=" " type="tel" pattern="[0-9]{9,25}$"/>
                        </div>
                        <label>{phoneLabel}</label>
                    </div>
                    <div className='inputContainer'>
                        <div className='interestInput'>
                            <Select
                                name="interestarea"
                                required
                                options={interestSelectOptions}
                                defaultValue={interestSelectOptions[0]}
                            />
                        </div>
                        <label>{interestAreaLabel}</label>
                    </div>
                    <div className='inputContainer'>
                        {/* <input name="projectbrief" required placeholder=" " type="text"/> */}
                        <textarea rows="3" name="projectbrief" required placeholder=" " type="text"></textarea>
                        <label>{projectBriefLabel}</label>
                    </div>
                </div>
            </div>
            <div className='meetingFromActions'>
                <div className='backButton' onClick={() => props.showPersonelFunction()}>
                    <ArrowBack />
                    <div>{backButton}</div>
                </div>
                <input className='submit' required placeholder=" " type="submit" value={submitButton}/>
            </div>
        </form>
    )
}

export default MeetingPersonalForm;