// components
import AboutUs from '../layouts/about_us/AboutUs';
import OurProjects from '../layouts/our_projects/OurProjects';

// style
import './Homepage.scss';

// react lifecycle
import {useEffect, useState} from 'react';
import { useParams } from 'react-router-dom';

// components
import OurServices from '../layouts/our_services/ourServices';
import OurPartners from '../layouts/our_partners/OurPartners';
import Insights from '../layouts/insights/Insights';
import ContactUs from '../layouts/contact_us/ContactUs';

// translation
import i18n from 'i18next';

function Homepage(props){

    const { lang } = useParams();
    
    useEffect(() => {
        i18n.changeLanguage(lang);
        // console.log(i18n.language);
    }, [lang])

    // react lifecycle
    useEffect(() => {
        // *****
        // enlarge image with scroll (then play video)
        // *****
        // get small image
        const smallImageContainer = document.querySelector('div.imageToVideo');
        const smallImage = document.querySelector('div.imageToVideo img');
        // get sibling image
        const smallSiblingImage = document.querySelector('div.homepageImagesContainerLeft:first-of-type img');
        // get image initital width
        const originalWidth = smallSiblingImage.width;
        // console.log(originalWidth);
        // get image initial height
        const originalHeight = smallSiblingImage.height;
        // get big video
        const bigVideo = document.querySelector('div.homepageLargeImage .videoContainer');
        // listen to scroll events
        window.addEventListener('scroll', (event) => {
            // store Y position of scroll
            let scrollY = document.documentElement.scrollTop;
            // console.log(scrollY)
            // if leaving top
            if(scrollY > 500 && scrollY < 950){
                // enlarge image
                smallImage.style.width = scrollY*originalWidth/500*(scrollY/500) + 'px';
                smallImage.style.height = scrollY*originalHeight/500*(scrollY/500) + 'px';
                // console.log(smallImage.style.width);
                // height auto
                smallImage.style.height = 'auto';
                // console.log(scrollY/originalWidth);
                // image diesply block (in case it was hidden)
                smallImage.style.display = 'block';
                // bideo visibility hidden (in case it was visible)
                bigVideo.style.visibility = 'hidden';
                smallImageContainer.style.visibility = 'visible';
                // console.log('visible')
            }
            // if reached vdeo area
            if(scrollY > 950){
                // image display none (in case it was visible)
                smallImage.style.display = 'none';
                // video visibility visible (in case it was hidden)
                bigVideo.style.visibility = 'visible';
                smallImageContainer.style.visibility = 'hidden';
                // console.log('hidden')
            }
            // if reached top
            if(scrollY < 500){
                // return image to its initial width and height
                smallImage.style.width = smallSiblingImage.width + 'px';
                // console.log('width: ' + originalWidth);
                smallImage.style.height = smallSiblingImage.height + 'px';
            }
        })
        // listen to window resize
        window.addEventListener("resize", (event) => {
            // return image to its initial width and height
            smallImage.style.width = smallSiblingImage.width + 'px';
            // console.log('width: ' + originalWidth);
            smallImage.style.height = smallSiblingImage.height + 'px';
        });
        // *****
        // *****
        // *****

        // *****
        // animation at top of the Home page "Reach Target Users"
        // *****
        // get all the three text elements
        const moveUpElement1 = document.querySelector('.secondSiteIntro .moveUp .i1')
        const moveUpElement2 = document.querySelector('.secondSiteIntro .moveUp .i2')
        const moveUpElement3 = document.querySelector('.secondSiteIntro .moveUp .i3')
        // "1" refers to each element's initial position
            let i1 = 1;
            let i2 = 1;
            let i3 = 1;
            // initiage loop
        const intervalId = setInterval(() => {
            // check for each element if it reached the top
            if (i1 > 1){
                i1 = -1; 
                // hide element if it is going to move down
                moveUpElement1.style.visibility = 'hidden';
            }else{
                // show the element if it is going to move up
                moveUpElement1.style.visibility = 'visible';
            }
            if (i2 > 2){
                i2 = 0;
                moveUpElement2.style.visibility = 'hidden';
            }else{
                moveUpElement2.style.visibility = 'visible';
            }
            if (i3 > 3){
                i3 = 1; 
                moveUpElement3.style.visibility = 'hidden';
            }else{
                moveUpElement3.style.visibility = 'visible';
            }
            // move elements whether top of down (according to the numbers above -1, 0, 1, 2, 3, 4)
            moveUpElement1.style.transform = `translateY(${-(100*i1)}%)`;
            moveUpElement2.style.transform = `translateY(${-(100*i2)}%)`;
            moveUpElement3.style.transform = `translateY(${-(100*i3)}%)`;
            // increment the counters
            i1++;
            i2++;
            i3++;
        }, 2500);
        // *****
        // *****
        // *****


        


        // *****
        // contact form
        // *****
        
        // *****
        // *****
        // *****


        // "return" is necessary to destroy the component when we close the website page (prevent infinity loop)
        return () => {
            clearInterval(intervalId);
            // clearInterval(animateBrandingId);
        }
        // *****
        // *****
        // *****


    }, []);

    return(
        <div className='homepageBody'>
            {/* About Us + Top + Video */}
            <AboutUs />
            {/* Our Services */}
            <OurServices />
            {/* Our Projects */}
            <OurProjects showCircleCursorFunction={props.showCircleCursorFunction}/>
            {/* Partners */}
            <OurPartners />
            {/* Insights */}
            {/* <Insights /> noteeee: this section is hidden */} 
            {/* Contact Us */}
            <ContactUs />
        </div>
    )
}

export default Homepage;