
// style
import './CircleMouseShape.scss';

// lifecycle
import { useEffect } from 'react';

function CircleMouseShape(props){

        useEffect(() => {

            const cursorBig = document.querySelector('.big');
            
            window.addEventListener('mousemove', (e) => {

                const mouseY = e.clientY;
                const mouseX = e.clientX;

                
                cursorBig.style.transform = `translate3d(${mouseX}px, ${mouseY}px, 0)`;
                // cursorBig.style.transform = `translate3d(1559px, 467px, 0)`;
                // console.log(`translate3d(${mouseX}px, ${mouseY}px, 0)`);
            })
        }, [])


        return(
            <div className={`cursorContainer ${props.showCircleCursor ? 'show' : ''}`}>
                <div className="cursor big">{props.circleCursorContect}</div>
            </div>
        )
}

export default CircleMouseShape;