// style
import './BookingPopup.scss';

// svg
import X from '../../assets/X';
import Clock from '../../assets/Clock';

// image
import ImageItemNoAnimation from '../../assets/ImageItemNoAnimation';

// logo
import Logo from '../../assets/Logo';

// react select
import Select from 'react-select';

// timezones
import timezones from '../../locales/json_files/timezones.json';

// lifecycle
import { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { Calendar } from 'react-calendar';
import MeetingPersonalForm from './MeetingPersonalForm';


function BookingPopup(props){

    // translation
    const { t, i18n } = useTranslation();
    //
    const [popupTitle, setPopupTitle] = useState('');
    const [popupSubTitle, setPopupSubTitle] = useState('');
    const [popupHighlight, setPOpupHighlight] = useState('');
    const [minutes, setMinutes] = useState('');
    const [popupDescription, setPopupDescription] = useState('');
    const [popupQuestion, setPopupQuestion] = useState('');
    const [popupQuestionSecond, setPopupQuestionSecond] = useState('');
    const [zoomCallTitle, setZoomCallTitle] = useState('');

    // set mins
    const [leftMins, setLeftMins] = useState('');
    const [rightMins, setRightMins] = useState('');

    useEffect(() => {
        // popup static text
        setPopupTitle(t("popup.book_a_meeting"));
        setPopupSubTitle(t("popup.where_meeting"));
        setPOpupHighlight(t("popup.thirty_minutes_marketing_consolution"));
        setMinutes(t("popup.minutes"));
        setPopupDescription(t("popup.schedule_in_30_minites"));
        setPopupQuestion(t("popup.how_long_you_need"));
        setPopupQuestionSecond(t("popup.what_times_for_you"));
        setZoomCallTitle(t("popup.zoom_with_ray"));
        setLeftMins(t("popup.thirty_mins"));
        setRightMins(t("popup.forty_mins"));
    }, [t])

    // set left select values
    const selectOptions = [
        { value: 'Office Meeting', label: 'Office Meeting' },
        { value: 'Online Meeting', label: 'Online Meeting' },
        { value: 'Zoom Meeting', label: 'Zoom Meeting' }
    ]

    // get array of hours
    const [hourPickerItems, setHourPickerValues] = useState([
        { value: '11:45 AM', label: '11:45 AM' },
        { value: '12:04 AM', label: '12:04 AM' },
        { value: '12:34 AM', label: '12:34 AM' },
        { value: '01:20 AM', label: '01:20 AM' },
        { value: '02:20 AM', label: '02:20 AM' },
        { value: '12:34 AM', label: '12:34 AM' }
    ]);
    // pick hour
    const [pickHour, setPickHour] = useState(0)
    // pick mins
    const [minSelectLeft, setMinSelectLeft] = useState(true);

    // get timezones
    const timezonesOptions = timezones;


    // show and hide meeting personal info form
    const [showPersonel, setShowPersonel] = useState(false);
    // function to hide form from child component
    function showPersonelFunction(booleanValue){
        setShowPersonel(booleanValue)
    }

    // calendar
    const [dateValue, setDateValue] = useState(new Date());
    // meeting schedule
    const [formattedDateValue, setFormattedDateValue] = useState(dateValue.toISOString().slice(0, 10));
    const [cutDate, setCutDate] = useState();
    //
    const [meetingHour, setMeetingHour] = useState(hourPickerItems[0].label);
    const [appointmentTimezone, setAppointmentTimezone] = useState('GMT-12:00');
    const [dateInput, setDateInput] = useState(new Date('1999-01-01').toISOString());
    //
    const [meetingDuration, setMeetingDuration] = useState(leftMins);

    // update hours
    useEffect(() => {
        // setDateValue(
            // console.log(appointmentTimezone);
            // console.log(dateValue.toString().slice(15, 24));
            // console.log(meetingHour);
            const dateTime = dateValue.toString().slice(0, 15);
            const timeString = meetingHour;
            // console.log(`${dateTime} ${timeString} ${appointmentTimezone}`)
            const dateObj = new Date(`${dateTime} ${timeString} ${appointmentTimezone}`);
            const time24Hour = dateObj.toLocaleTimeString('en-US', {
                weekday: 'long',
                month: 'long',
                day: 'numeric',
                hour: 'numeric',
                minute: 'numeric',
                hour12: false,
                timeZoneName: 'short'
                // timeZone: appointmentTimezone
            });
            // console.log(time24Hour); // "11:45:00"
            setFormattedDateValue(time24Hour);
            // const timeForInput = dateObj.toLocaleDateString('en-US', {
            //     year: 'numeric',
            //     month: 'numeric',
            //     day: 'numeric',
            // })
            const yy = dateObj.getFullYear().toString().padStart(4, '0');;
            const mm = dateObj.getMonth().toString().padStart(2, '0');;
            const dd = dateObj.getDate().toString().padStart(2, '0');;
            // console.log(formattedDate2);
            setDateInput(`${yy}-${mm}-${dd}`);
        // )
    }, [dateValue, meetingHour, appointmentTimezone, formattedDateValue])

    return(
        <div className='bookingPopupContainer' style={{ display : `${props.shown ? 'flex' : 'none'}` }}>
            <div className='visibleArea'>
                <div className='popupTitle'>
                    <div className='xIcon' onClick={() => props.hidePopupFunction()}>
                        <X />
                    </div>
                    {popupTitle}
                </div>
                <div className='popupContent'>
                    <div className='popupLeft'>
                        <Logo />
                        <div className='normalText'>
                            {popupSubTitle}
                        </div>
                        <Select
                            options={selectOptions}
                            defaultValue={selectOptions[1]}
                        />
                        <div className='popupSubTitle'>
                            {popupHighlight}
                        </div>
                        <div className='timeInMinutes'>
                            <div className='clockIcon'>
                                <Clock />
                            </div>
                            <div>30 {minutes}</div>
                        </div>
                        <div className='scheduleDescription'>{popupDescription}</div>
                    </div>
                    <div className={`popupRight ${showPersonel? 'hide': ''}`}>
                        <div className='rightPopupHighlited'>
                            <div className='customerSeviceImage'>
                                <ImageItemNoAnimation imageLink={ global.imageUserAgent } />
                            </div>
                            <div className="zoomCallTitle">{zoomCallTitle}</div>
                            <div className='timeTableArea'>
                                <Calendar onChange={setDateValue} value={dateValue} locale={i18n.language === 'en' ? 'en-EN' : 'ar-AR'}/>
                            </div>
                        </div>
                        <div className='rightPopupBright'>
                            <div className='popupQuestion'>
                                {popupQuestion}
                            </div>
                            <div className='minutesNeeded'>
                                <div className={`leftMinutes ${minSelectLeft ? 'selected' : ''}`} onClick={() => {setMinSelectLeft(true); setMeetingDuration(leftMins)}}>
                                    {leftMins}
                                </div>
                                <div className={`rightMinutes ${minSelectLeft ? '' : 'selected'}`} onClick={() => {setMinSelectLeft(false); setMeetingDuration(rightMins)}}>
                                    {rightMins}
                                </div>
                            </div>
                            <div className='popupQuestionSecond'>
                                {popupQuestionSecond}
                            </div>
                            <div className='timeZoneSelect'>
                                <Select
                                    options={timezonesOptions}
                                    onChange={(choice) => setAppointmentTimezone(choice.name)}
                                    defaultValue={timezonesOptions[65]}
                                />
                            </div>
                            <div className='hourPicker'>
                                {hourPickerItems.map((hour, index) => <div key={index} id={index} onClick={() => {setPickHour(index); setShowPersonel(true); setMeetingHour(hour.label)}} className={`hourPickerItem ${(pickHour === index? 'selected': '')}`}>{hour.label}</div>)}
                            </div>
                        </div>
                    </div>
                    <MeetingPersonalForm
                        // toggle popup
                        showPersonel={showPersonel}
                        showPersonelFunction={showPersonelFunction}
                        // date
                        meetingDuration={meetingDuration}
                        meetingHour={meetingHour}
                        formattedDateValue={formattedDateValue}
                        dateInput={dateInput}
                        // cut date
                        cutDate={cutDate}
                        // calendar
                        dateValue={`${dateValue.toString().slice(0, 15)}`}
                    />
                </div>
            </div>
        </div>
    )
}

export default BookingPopup;
